@import '../../globalCss/_variables.scss';
@import '../../globalCss/_mixin.scss';
// TODO: RWD 


.headerContainer {
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 10vh;
    max-height: 70px;
    background-color: white;
    box-shadow: 0px 2px 5px rgb(0 0 0 / 50%);
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.headerPad {
    position: relative;
    height: 10vh;
    max-height: 70px;
}


.title {
    @include absobuteLoc(50%, 50%);
    color: $cPurple;
    font-size: calc(10px + 2vmin);

    a {
        color: $cPurple;
        text-decoration: none;
    }
}


.sign {
    padding: 0 10px;

    @include animHoverUp;

    a {
        text-decoration: none;
        color: $cGray;
    }

    //TODO: replace with
    // @include pad {
    //     &::before {
    //         content: url(https://icons.veryicon.com/png/o/phone/jj/login-19.png);
    //     }
    // }

    @include mobile {
        display: none;
    }
}